:root {
  --font-family: "myFont", "sans-serif";
  --dark-blue: #0a1930;
  --light-blue: #1f93ff;
  --color-white: #fff;
  --dark-text:#333;
  --form-button: #2da5ff;
  --color-gray: #eae6eb;
  --box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  text-decoration: none;
  list-style: none;
  outline: none;
  font-family: 'myFont';
}

html {
  font-size: 10px;
}

body{
  font-family: 'myFont', 'sans-serif';
  overflow-x: hidden;

}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
}

.header-bg {
    background-color: #0a1930;
    width: 100%;
    height: 100%;
    min-height: 100vh;
}

section {
    padding: 12rem 0;
    width: 100%;
}

a {
  color: #fff;
    font-size: 16px;
    margin-right: .6rem;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
}

a.btn {
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  padding: 5px 8px;
}

a.btn-dark {
  border: 2px solid #fff;
}

a.btn-dark:hover {
  color: orangered;
}

a.btn-light {
  background: orangered;
  border: 2px solid transparent;
  color: var(--color-white);
}